import React from "react";
import { useState } from "react";
import PhoneInput from "react-phone-input-2";
import "react-phone-input-2/lib/style.css";
function Communication() {
  const [phone, setPhone] = useState("");

  return (
    <div>
      <section className="communication-home mx-auto my-5">
        <div className="container">
          <div className="row">
            <div className="col-md-6 text-right mt-5 left-communication">
              <h1>"!  -   "</h1>
              <p className="mt-4">
                         
                .  <br />
                     .    
                 
                <br />     [ ]
              </p>
              <div className="col-md-12 mb-4">
                <h3 className="mr-2"> </h3>
                <img src="/Assets/img/Group 20623.png" />
                <h6>Admin@shogol.sa</h6>
              </div>
              <div className="col-md-12 mb-4">
                <h3 className="mr-2"> </h3>
                <img src="/Assets/img/Group 20623.png" />
                <h6>Admin@shogol.sa</h6>
              </div>
              <div className="col-md-12 mb-4">
                <h3 className="mr-2"> </h3>
                <img src="/Assets/img/Group 20623.png" />
                <h6>Admin@shogol.sa</h6>
              </div>
            </div>
            <div className="col-md-6 right-communication text-right mt-5">
              <form>
                <h3 className="mb-3"> </h3>
                <div>
                  <PhoneInput
                    country={"sa"} //  
                    value={phone}
                    onChange={(value) => setPhone(value)}
                    inputStyle={{
                      width: "100%",
                      marginLeft: "50px",
                      borderRadius: "12px",
                      height: "40px",
                    }}
                    buttonStyle={{
                      border: "none",
                      backgroundColor: "transparent",
                      paddingLeft: "50px",
                    }}
                    countryCodeEditable={false}
                    enableSearch={true}
                    disableDropdown={false}
                    masks={{ sa: "....-...-...." }} //      
                    placeholder="5292-282-1572"
                  />
                </div>

                <h3 className="mr-5 mb-3 mt-5"> </h3>
                <h3 className="ml-5"> </h3>
                <div className="form-row  text-form ml-auto">
                  <div className="input-group has-validation col-md-5 text-right">
                    <input
                      type="text"
                      className="form-control text-right"
                      required
                      placeholder="admin@shogol-sa.com"
                    />
                    <div className="invalid-feedback">
                      Please choose a username.
                    </div>
                  </div>

                  <div className="input-group has-validation col-md-5">
                    <input
                      type="email"
                      className="form-control text-right"
                      required
                      placeholder="  "
                    />
                    <div className="invalid-feedback">
                      Please choose a username.
                    </div>
                  </div>
                </div>
                <h3 className="mb-3 mt-5 col-md-13"> </h3>
                <textarea
                  name="message"
                  placeholder="... "
                  rows={"5"}
                  maxLength={"50"}
                  minLength={"5"}
                  className="col-md-12 text-right form-control"
                />
                <button
                  type="submit"
                  class="btn mb-2 col-md-12 submit-button mt-2"
                >
                  
                </button>
              </form>
            </div>
          </div>
        </div>
      </section>
    </div>
  );
}

export default Communication;
