import React from "react";
import { Link } from "react-router";
function Navbar() {
  return (
    <div>
      {/* Start Navbar Home Section */}

      <nav
        className="navbar navbar-expand-lg navbar-light navbar-home"
        id="Navbar-drop"
      >
        <img src="/Assets/img/Group 29.png" className="ml-5" />
        <div class="dropdown">
          <button
            class="btn buttona"
            type="button"
            data-toggle="dropdown"
            aria-expanded="false"

          >
            <img src="/Assets/img/Group 20731.png" className="mr-4" />
          </button>
          <div class="dropdown-menu animator">
            <a class="dropdown-item" href="#">
              Action
            </a>
            <a class="dropdown-item" href="#">
              Another action
            </a>
            <a class="dropdown-item" href="#">
              Something else here
            </a>
          </div>
        </div>

        <button
          className="navbar-toggler"
          type="button"
          data-toggle="collapse"
          data-target="#navbarSupportedContent"
          aria-controls="navbarSupportedContent"
          aria-expanded="false"
          aria-label="Toggle navigation"
        >
          <span className="navbar-toggler-icon" />
        </button>

        <div className="collapse navbar-collapse" id="navbarSupportedContent">
          <ul className="navbar-nav mr-auto">
            <li className="nav-item active">
              <a className="nav-link Navbar-home" href="#">
                  
                <img src="/Assets/img/Group 20591.png" />
                <span className="sr-only">(current)</span>
              </a>
            </li>

            <li className="nav-item Navbar-ads">
              <a className="nav-link" href="#">
                 
              </a>
            </li>

            <li className="nav-item navbar-icon mx-3 mt-3">
              <a href="#">
                <img src="/Assets/img/Group 21004.png" />
              </a>
            </li>

            <li className="nav-item navbar-icon mx-3 mt-3">
              <a href="#">
                <img src="/Assets/img/bells 1.png" />
              </a>
            </li>
          </ul>

          <ul className="navbar-nav ml-auto Navbar-content">
            <li className="nav-item">
              <a href="#"> </a>
            </li>
            <li className="nav-item mx-3">
              <a href="#"> </a>
            </li>

            <li className="nav-item">
              <a href="#"> </a>
            </li>

            <li className="nav-item mx-3">
              <Link to="/home"></Link>
            </li>
          </ul>

          <img src="/Assets/img/Group 5.png" className="mx-5" />
        </div>
      </nav>

      {/* End Navbar Home Section */}
    </div>
  );
}

export default Navbar;
