import React from "react";

function SubNavbar() {
  return (
    <div>
      <nav className="navbar Second-navbar">
        <div className="dropdown">
          <button
            className="btn"
            type="button"
            data-toggle="dropdown"
            aria-expanded="false"
          >
            <img src="/Assets/img/Group 388.png" className="ml-5" />
          </button>
          <div className="dropdown-menu">
            <a className="dropdown-item" href="#">
              Action
            </a>
            <a className="dropdown-item" href="#">
              Another action
            </a>
            <a className="dropdown-item" href="#">
              Something else here
            </a>
          </div>
        </div>
        <button className="mr-auto Second-content"> </button>

        <p className="mr-2">
          "EliteTalents:     !"
        </p>
        <div className="Navbar2-img">
          <img src="/Assets/img/diploma 2.png" id="img" />
        </div>
      </nav>
    </div>
  );
}

export default SubNavbar;
